<template>
  <div class="image-gallery">
    <div v-if="images.length === 0">No images found in the specified directory.</div>
    <div v-for="image in images" :key="image" class="image-container">
      <img :src="image" alt="Image" @click="showImage(image)" />
    </div>
    <div v-if="showModal">
      <div class="modal">
        <img :src="selectedImage" alt="Enlarged Image" />
        <button @click="closeModal">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ImageGallery',
  props: {
    folder: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      images: [],
      showModal: false,
      selectedImage: ''
    };
  },
  created() {
    this.loadImages();
  },
  methods: {
    loadImages() {
      try {
        console.log(`Loading images from folder: ${this.folder}`);
                // const imagesContext = require.context(`../assets/${this.folder}`, false, /\.(png|jpe?g|svg)$/);
        const imagesContext = require.context(`../assets/img2`, false, /\.(png|jpe?g|svg|webp)$/);
        this.images = imagesContext.keys().map(imagesContext);
        console.log('Images loaded:', this.images);
      } catch (e) {
        console.error(`Failed to load images from folder: ${this.folder}`, e);
        this.images = [];
      }
    },
    showImage(image) {
      this.selectedImage = image;
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
    }
  }
};
</script>

